import {mapMutations} from 'vuex'
export default {
  data() {
    return {version: 'V0.0.0.1'}
  },
  mounted() {},
  methods : {
    ...mapMutations(['closeDialog', 'openDialog']),
    toPage(routerName) {
      this
        .$router
        .push({name: routerName});
    },
    toAboutUs() {
      window.location.href = 'http://www.jzetech.com/index.php?p=show&c=template&a=company'
    },
    toNewsCenter() {
      window.location.href = 'http://www.jzetech.com/index.php?p=show&c=template&a=news'
    },
    makeWaterMark(ele, imgSrc, img2Src, width, height) {
      // 合成水印图片
      let canvas = document.createElement('canvas');
      canvas.width = width;
      canvas.height = height;
      let ctx = canvas.getContext("2d");
      // 底图
      let img = new Image();
      img.src = imgSrc
      img.onload = () => {
        ctx.drawImage(img, 0, 0);
        // 水印图片
        let img2 = new Image();
        img2.src = img2Src
        img2.onload = () => {
          var ptrn = ctx.createPattern(img2, 'repeat');
          ctx.fillStyle = ptrn;
          ctx.fillRect(0, 0, width, height);
          ele.appendChild(canvas)
        };
      };
    }
  }
}
