<template>
  <div class="xy-header">
    <div class="header">
      <div class="xy-min-width flex flex-x-between flex-y-center">
        <div class="xy-logo">
          <div class="flex flex-x-between flex-y-center">
            <!-- <strong>源生汇·智慧溯源</strong> -->
            <img @click="toPage('home')" src="../assets/imgs/home_slices/logo_zhsy.png" alt>
            <span>食安、心安、民安</span>
          </div>
        </div>
        <div class="xy-nav-warp">
          <div class="active-line" :style="activeLineStyle"></div>
          <ul class="xy-nav flex flex-y-center">
            <template v-for="d in navList">
              <li v-if="d.key===1" @mouseover="navMouseover('solutionlv2')" @mouseleave="navMouseleave('solutionlv2')" :class="d.active?'active':''" @click="navListCLick(d)">{{d.name}}</li>
              <li v-else-if="d.key===2" @mouseover="navMouseover('productlv2')"  @mouseleave="navMouseleave('productlv2')" :class="d.active?'active':''" @click="navListCLick(d)">{{d.name}}</li>
              <li v-else :class="d.active?'active':''" @click="navListCLick(d)">{{d.name}}</li>
            </template>
          </ul>
        </div>
      </div>
    </div>
    <!-- 解决方案下拉 -->
    <div
      @mouseover="navMouseover('solutionlv2')"
      @mouseleave="navMouseleave('solutionlv2')"
      ref="solutionlv2"
      class="solutionlv2 flex flex-x-center flex-y-center"
    >
      <div @click="toPage('mainProduct')">重要产品追溯</div>
      <div @click="toPage('wisdomFarm')">智慧农批</div>
      <!-- <div @click="toPage('wisdomEnvProtection')">智慧环保</div> -->
      <div @click="toPage('schoolFoodSafety')">校园食品安全</div>
      <div @click="toPage('helpPoor')">追溯扶贫</div>
    </div>
    <!-- 系列产品下拉 -->
    <div
      @mouseover="navMouseover('productlv2')"
      @mouseleave="navMouseleave('productlv2')"
      ref="productlv2"
      class="productlv2 flex flex-x-center flex-y-center"
    >
      <ul>
        <li>生产加工</li>
        <li @click="toPage('farmProduce')">农产品质量安全追溯系统</li>
        <li @click="toPage('livestockProduce')">畜禽产品质量安全追溯系统</li>
        <li @click="toPage('slaughter')">屠宰企业信息化追溯系统</li>
        <li @click="toPage('dairy')">乳制品信息化追溯系统</li>
      </ul>
      <ul>
        <li>流通消费</li>
        <li @click="toPage('directSupply')">产地直供系统</li>
        <li @click="toPage('groupPurchasing')">团体采购/批发销售系统</li>
        <li @click="toPage('freshRetail')">生鲜零售系统</li>
        <li @click="toPage('freshDistribution')">生鲜配送系统</li>
      </ul>
    </div>
  </div>
</template>
<script>
import mixin from "../util/mixin";
export default {
  name: "Header",
  props: {},
  mixins: [mixin],
  data() {
    return {
      activeLineStyle:{
        width: `28px`,
        left: `27px`
      },
      navList: [
        {
          key: 0,
          name: "首页",
          route: "home",
          active: true
        },
        {
          key: 1,
          name: "解决方案",
          route: "mainProduct",
          active: false
        },
        {
          key: 2,
          name: "产品系列",
          route: "farmProduce",
          active: false
        },
        {
          key: 3,
          name: "新闻中心",
          route: "news",
          active: false
        },
        {
          key: 4,
          name: "关于我们",
          route: "about",
          active: false
        }
      ]
    };
  },
  // 监听,当路由发生变化的时候执行
  watch: {
    $route(to, from) {
      let activeName = to.name
      let solutionRouters = ["mainProduct","wisdomFarm","schoolFoodSafety","helpPoor"]
      let produceRouters = ["directSupply","groupPurchasing","freshRetail","freshDistribution","dairy","slaughter","vestockProduce","farmProduce"]
      if(solutionRouters.indexOf(to.name) >= 0){
        activeName = "mainProduct"
      }
      if(produceRouters.indexOf(to.name) >= 0){
        activeName = "farmProduce"
      }
      // 解决方案产品系列导航选中
      this.navList.map(item => {
        if (activeName == item.route) {
          item.active = true;
          this.activeLineStyle.width = `${$("ul.xy-nav li").eq(item.key).width()}px`
          this.activeLineStyle.left = `${$("ul.xy-nav li").eq(item.key).position().left+27}px`
        } else {
          item.active = false;
        }
      });
    }
  },
  mounted() {
    // 鼠标移入active效果
    $(document).on("mouseover", "ul.xy-nav li", function() {
      $(".active-line").css({
        width: $(this).width() + "px",
        left: $(this).position().left + 27 + "px"
      });
    });
    // 鼠标移除active效果
    $(document).on("mouseleave", "ul.xy-nav li", function() {
      $("ul.xy-nav li").each((index, element) => {
        if ($(element).attr("class") == "active") {
          $(".active-line").css({
            width: $(element).width() + "px",
            left: $(element).position().left + 27 + "px"
          });
        }
      });
    });
  },
  methods: {
    navMouseover(nav) {
      this.$refs[nav].style.top = "70px";
    },
    navMouseleave(nav) {
      let h = this.$refs[nav].clientHeight || this.$refs[nav].offsetHeight;
      this.$refs[nav].style.top = `-${h}px`;
    },
    navListCLick(d) {
      if (!d.active) {
        d.active = true;
      }
      this.navList.map(item => {
        if (d.key != item.key) {
          item.active = false;
        }
      });
      // 页面跳转
      if (d.route === "news") {
        this.toNewsCenter();
      } else {
        this.toPage(d.route);
      }
    }
  }
};
</script>
<style scoped lang="scss">
.xy-header {
  .solutionlv2 {
    width: 100%;
    height: 100px;
    position: fixed;
    background: #0e192c;
    border-top: 1px solid #5d6572;
    top: -100px;
    color: #fff;
    transition: all 0.7s;
    font-size: 20px;
    z-index: 10;
    opacity: 0.9;
    div {
      padding: 0 50px;
      cursor: pointer;
    }
    div:hover {
      color: #3089dc;
    }
  }
  .productlv2 {
    width: 100%;
    height: 300px;
    position: fixed;
    background: #0e192c;
    border-top: 1px solid #5d6572;
    top: -300px;
    color: #fff;
    transition: all 0.7s;
    font-size: 20px;
    z-index: 10;
    opacity: 0.9;

    ul {
      li:not(:nth-child(1)) {
        padding: 10px 0;
        cursor: pointer;
      }
      li:not(:nth-child(1)):hover {
        color: #3089dc;
      }
      li:nth-child(1) {
        font-size: 16px;
        color: #ddd;
        padding: 10px 0;
      }
    }
    ul:nth-child(1) {
      margin-right: 180px;
    }
  }
  .header {
    width: 100%;
    position: fixed;
    height: 70px;
    top: 0;
    background: #14253d;
    color: #fff;
    z-index: 100;
    .xy-min-width {
      height: 100%;
      .xy-logo {
        line-height: 70px;
        img {
          cursor: pointer;
        }
        strong {
          font-size: 24px;
          font-family: "PingFang-SC-Medium";
        }
        span {
          color: #b9d1e0;
          font-size: 14px;
          font-family: "SourceHanSansSC-Normal";
          padding-left: 20px;
        }
      }
      .xy-nav-warp {
        position: relative;
        .active-line {
          height: 3px;
          background: #3089dc;
          width: 28px;
          position: absolute;
          bottom: 0;
          border-radius: 3px;
          left: 27px;
          transition: all 0.3s;
        }
        ul.xy-nav {
          height: 100%;
          li {
            line-height: 68px;
            font-size: 14px;
            cursor: pointer;
            padding: 0 27px;
            transition: all 0.3s;
          }
          li:hover {
            font-weight: bolder;
            color: #3089dc;
            // border-bottom: 2px solid #3089dc;
          }
          li:last-child {
            margin-right: 0;
          }
          li.active {
            font-weight: bolder;
            color: #3089dc;
            // border-bottom: 2px solid #3089dc;
          }
        }
      }
    }
  }
}
</style>
