<template>
  <div id="app">
    <Header/>
    <div class="xy-content">
      <router-view/>
    </div>
    <Footer @openDialog="openDialog"/>
    <el-dialog
      title="服务热线：400-8558-783"
      :visible.sync="dialogVisible"
      width="620px"
      :before-close="closeDialog"
      top="25vh"
    >
      <el-form :rules="rules" ref="form" :model="form" label-width="115px">
        <el-form-item label="企业名称：" prop="QYname">
          <el-input v-model="form.QYname"></el-input>
        </el-form-item>
        <el-form-item label="联系电话：" prop="tel">
          <el-input v-model="form.tel"></el-input>
        </el-form-item>
        <el-form-item label="姓名：" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label>
          <p style="font-size:14px;color:#FF4800">我们将会在1-3个工作日联系你，请注意保持手机通畅</p>
        </el-form-item>
        <el-form-item>
          <div class="flex flex-x-end">
            <div class="okbtn" type="primary" @click="onSubmit">确定</div>
            <div class="cancalbtn" type="primary" @click="closeDialog">取消</div>
          </div>
        </el-form-item>
      </el-form>
    </el-dialog>
    <backTop/>
  </div>
</template>
<script>
// @ is an alias to /src
import Header from "@/components/Header.vue";
import Footer from "@/components/Footer.vue";
import backTop from "@/components/backTop.vue";
import mixin from "@/util/mixin";
export default {
  mixins: [mixin],
  name: "App",
  components: {
    Header,
    Footer,
    backTop
  },
  mounted() {
    this.$router.afterEach((to, from, next) => {
      window.scrollTo(0, 0);
    });
  },
  computed: {
    dialogVisible() {
      return this.$store.state.dialogVisible;
    }
  },
  data() {
    return {
      form: {
        QYname: "",
        name: "",
        tel: ""
      },
      rules: {
        QYname: [
          { required: true, message: "请输入企业名称", trigger: "blur" },
          { min: 3, max: 20, message: "长度在 3 到 20 个字符", trigger: "blur" }
        ],
        tel: [
          { required: true, message: "请输入联系电话", trigger: "blur" },
          { max: 11, message: "长度为11个字符", trigger: "blur" }
        ],
        name: [
          { required: true, message: "请输入姓名", trigger: "blur" },
          { min: 3, max: 20, message: "长度在 3 到 20 个字符", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    onSubmit() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          console.log(this.form);
          this.closeDialog();
          this.$message({
            message:
              "提交成功，我们将会在1-3个工作日联系你，请注意保持手机通畅",
            type: "success"
          });
        } else {
          return false;
        }
      });
    }
  }
};
</script>

