import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    dialogVisible: false
  },
  mutations: {
    closeDialog(state) {
      state.dialogVisible = false
    },
    openDialog(state) {
      state.dialogVisible = true
    }
  },
  actions: {}
})
